import { ArticleType } from '../types/article';

const ARTICLE_DATA_MOCK = {
    title: 'Luc Dupanloup',
    text: `Luc Dupanloup, pseud. Dupa (ur. 13 lutego 1945 w Montignies-sur-Sambre, zm. 8 listopada 2000 w Ottignies)  belgijski rysownik, twrca postaci Cubitusa (w niektrych krajach znanej te jako Dommel).
Luc Dupanloup studiowa na Akademii Sztuk Piknych w Brukseli w latach 60. Karier rozpocz w 1968, kiedy to stworzy posta Cubitusa dla belgijskiego magazynu "Tintin". Od tego czasu przygody sympatycznego, puszystego, biaego psa stay si bardzo popularne i przyniosy sukces komercyjny. Jako asystent Grega, Dupanloup pracowa rwnie nad innymi komiksami, takimi jak Fifi, Fleurette, Luc Junior, Tootsy i Achille Talon.
W roku 1972 Dupanloup przej popularn seri "Chlorophylle" od Raymonda Macherota (z tekstem napisanym przez Grega i Bobiego de Groota). Napisa w tym czasie rwnie scenariusz dla serii "ric". W 1973 zosta nagrodzony Crayon d'Argent. W roku 1980 roku Dupanloup stworzy popularn seri "Kiky" o kierowcy ciarwki.
W 1994 La Poste (belgijska poczta) wypucia specjalny, okolicznociowy znaczek pocztowy z Cubitusem.`,
};

export function fetchArticle() {
    return new Promise<{ data: ArticleType }>((resolve) =>
        setTimeout(() => resolve({ data: ARTICLE_DATA_MOCK }), 500)
    );
}
